"use client"

import { createContext, useContext, useState, useEffect, ReactNode } from "react"
import { translations, Language, Translation } from "@/translations"

interface LanguageContextType {
  language: Language
  setLanguage: (lang: Language) => void
  t: Translation
}

const defaultLanguage: Language = "en"

const defaultContext: LanguageContextType = {
  language: defaultLanguage,
  setLanguage: () => {},
  t: translations[defaultLanguage]
}

const LanguageContext = createContext<LanguageContextType>(defaultContext)

export function LanguageProvider({ children }: { children: ReactNode }) {
  const [language, setLanguageState] = useState<Language>(defaultLanguage)
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    try {
      // Get saved language from localStorage or use browser language
      const savedLanguage = localStorage.getItem("language") as Language
      const browserLanguage = navigator.language.split("-")[0] as Language
      const initialLanguage = savedLanguage || (translations[browserLanguage] ? browserLanguage : defaultLanguage)
      setLanguageState(initialLanguage)
    } catch (error) {
      // Fallback to default language if localStorage is not available
      setLanguageState(defaultLanguage)
    }
    setMounted(true)
  }, [])

  const setLanguage = (lang: Language) => {
    try {
      setLanguageState(lang)
      localStorage.setItem("language", lang)
    } catch (error) {
      console.error("Failed to save language preference:", error)
    }
  }

  const value = {
    language,
    setLanguage,
    t: translations[language]
  }

  // During SSR and initial client-side render, use default translations
  if (!mounted) {
    return (
      <LanguageContext.Provider value={defaultContext}>
        {children}
      </LanguageContext.Provider>
    )
  }

  return (
    <LanguageContext.Provider value={value}>
      {children}
    </LanguageContext.Provider>
  )
}

export function useLanguage() {
  return useContext(LanguageContext)
}
